.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px  rgb(0, 0, 0, 0.75);
    max-width: 700px;
    justify-content: center; 
    margin-bottom: 45px;
    
}
.messageSender__top{
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
}
.messageSender__top > form {
    flex: 1;
    display: flex;
}

.messageSender__top > form > input{
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}

.messageSender__top > form > button{
    border-radius: 999px;
}